@import '../modules';

// pagination Í≥µÌÜµ ÏÜçÏÑ±
.pagination{
  display: flex;
  justify-content: center;
  align-items: center;
  
  // arrow Î∞©Ìñ• ÌÇ§ Í≥µÌÜµÏÜçÏÑ±
  .btn-ar{
    width: 2rem;
    height: 2.6rem;

    // ÏôºÏ™Ω Î∞©Ìñ•Ïùº Îïå, ÌöåÏ†ÑÏãúÌÇ¥
    &.--left{
      margin-left: 8px;
      transform: rotate(180deg);
    }
    // Ïò§Î•∏Ï™Ω Î∞©Ìñ•Ïùº Îïå,
    &.--right{
      margin-right: 8px;
    }
    
    // Î≤ÑÌäº ÌÅ¥Î¶≠ Î∂àÍ∞ÄÎä•
    &.--disable{
      pointer-events: none;
    }

    // Î≤ÑÌäº ÌÅ¥Î¶≠ Í∞ÄÎä•
    &.--able{
      svg path{
        fill: #797979;
        transition: fill 0.3s;
      }
    }

    .svg{
      width: 2rem;
      height: 2.6rem;
    }
  }

  // pagination Ïà´ÏûêÎì§
  .pagination-nums{
    display: inline-flex;
    align-items: center;
    padding: 0 16px;

    li button{
      width: 3.6rem;
      height: 3rem;
      color: $font_color-797979_grey;

      &:hover{
        text-decoration: underline;
      }
    }

    // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ Ïà´Ïûê
    .now{
      button{
        color: $azure;
        font-weight: 700;
      }
    }
  }
}

// pagination Íµ¨Î¶Ñ Ïª§ÎÆ§ÎãàÌã∞
.pagination.commu{
  transform: translateY(-1rem);
  ol li.now button{
    color: $commu_cyan_01;
  }
}


// * Î≤ÑÌäº - Î™©Î°ùÎ≥¥Í∏∞
.btn-link-list{
  display: inline-flex;
  align-items: center;
  height: 5.8rem;
  padding: 0 4.4rem;
  border-radius: 4rem;

  color: #666666;
  border: 1px solid #B0B0B0;

  background-color: white;
  transition: all 0.2s;
  
  .icon{
    margin-right: 1rem;
  }

  // ÎèôÏûëÏ†ïÏùò
  &:hover{
    color: #3C3C3C;
    border: 1px solid #666666;
  }

  &:active{
    color: #212121;
    border: 1px solid #3C3C3C;
  }
}


// * üëΩÔ∏èresponsive
@include mobile-max{
  .pagination{
    width: 100%;

    .btn-ar{
      
      .svg{
        width: 1.6rem;  
        height: 2.1rem;

      }
    }

    .pagination-nums{
      padding: 0 12px;
      column-gap: 0.2rem;
    }
  }

  .btn-link-list{
    height: 3.6rem;
    padding: 0 2.6rem;

    font-size: 1.2rem;

    .icon{
      width: 1.8rem;
      height: 1.8rem;
    }
  }

}